            this.addressList = res.data.data;
          } else {
          }
        })
        .catch(error => {});
    },
    //
    handleSelectionChange(val) {
      this.arrResume = [];
      this.arrPosition = [];
      this.multipleSelection = val;
      this.multipleSelection.forEach((item, index, array) => {
        this.arrResume.push(item.id);
        this.arrPosition.push(item.positionId);
      });
    },
    uploadFile() {
      // let resumeList = qs.stringify(
      //   {
      //     resumeIds: this.arrResume,
      //     positionIds: this.arrPosition
      //   },
      //   { arrayFormat: "repeat" }
      // );
      let resumeList = {
        resumeIds: this.arrResume,
        positionIds: this.arrPosition
      };
      this.dialogVisible = true;
      this.$local
        .post("/business-core/resumes/batchPackageDownloadLong", resumeList, {
          responseType: "blob"
        })
        .then(res => {
          console.log(res.headers);
          this.dialogVisible = false;
          const disposition = res.headers["content-disposition"];
          console.log(disposition);
          let fileName = disposition.substring(
            disposition.indexOf("filename=") + 9,
            disposition.length
          );
          // iso8859-1
          fileName = decodeURI(escape(fileName));
          console.log(fileName);
          // 
          fileName = fileName.replace(/\"/g, "");
          const content = res.data;
          let blob = new Blob([res.data], {
            type: "application/vnd.ms-excel"
          });
          console.log(blob);
          if (window.navigator.msSaveOrOpenBlob) {
            // console.log(2)
            navigator.msSaveBlob(blob, fileName);
          } else {
            // console.log(3)
            var link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.download = fileName;
            link.click();
            //
            window.URL.revokeObjectURL(link.href);
          }
        })
        .catch(error => {
          this.dialogVisible = false;
          if (error.response.status === 404) {
            this.$notify.info({
              title: "",
              message: ""
            });
          } else if (error.response.status === 403) {
            this.$notify.info({
              title: "",
              message: ""
            });
          } else {
            this.$notify.info({
              title: "",
              message: error.response.data.message
            });
          }
        });
    },
